
import { web3Service } from '@/web3/web3Service';
import { defineComponent } from 'vue';

export default defineComponent({
  setup() {
    const status = web3Service.status();
    const connect = () => {
      web3Service.init();
    };
    return {
      status,
      connect,
      publicPath: process.env.BASE_URL,
    };
  },
});
